
:root {
  --font-base: 'Cormorant Upright', serif;
  --font-alt: 'Open Sans', sans-serif;
  --color-golden: #DCCA87;
  --color-black: #0C0C0C;
  --color-gray: #545454;
  --color-crimson: #F5EFDB;
  --color-grey: #AAAAAA;
  --color-white: #FFFFFF;
  --color-bg:#1f1f38;
  --color-bg-variant:#2c2c6c;
  --color-primary-varient:rgba(77,181,255,0.4);
  --color-light:rgba(255,255,255,0.6);
  --color-primary:#4db5ff;

  --container-width-lg:75%;
  --container-width-md:86%;
  --container-width-lg:90%;
}

.profile-container {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  height: 10%;
  min-height: fit-content;
  background-color:#212529;
  
  
}
.profile-parent {
  display: flex;
  align-items: center;
  color: aliceblue;
}
.profile-details-name {
  font-size: 24px;
  font-family: 'poppins semiBold';
  margin: 14px 0 24px 0;
}
.profile-details-role h1 {
  font-size: 40px;
  font-family: cursive;
}
.primary-text {
  color:(--white);
}
.highlighted-text {
  color: #ff5823;
}
.profile-role-tagline {
  font-size: 19px;
  margin: 5px 0 0 0;
  font-family: 'poppins light';
}
.profile-picture {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 1px 0px 0px var(--white);
  border-radius: 50%;
  height: 380px;
  width: 380px;
  margin: 0 0px 0 100px;
}
.profile-picture-background {
  height: 92%;
  width: 92%;
  background-size: cover;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center;
}
.profile-picture-background:hover {
  transform: scale(1.07);
  transition: 1s ease-out;
}
.colz.colz-icon i {
  color: white;
  padding: 8px;
  padding-left: 0px;
}

@media screen and (max-width:1110px) {
  .profile-picture {
    margin: 0 0px 0 80 px;
  }
  .profile-details-name{
    font-size: 20px;
  }
  .profile-details-role .primary-text {
    font-size: 12px;
  }
  .profile-options .btn {
    width: 120px;
    padding: 11px 0;
    z-index: 700;
  }
}
@media screen and (max-width:810px) {
  .profile-picture {
    height: 320px;
    width: 320px;
    margin: 0 0 0 4px;
  }
}
@media screen and (max-width:810px) {
  .profile-picture{
    margin: 0 !important;
  }
  .profile-parent{
    flex-direction: column-reverse;
    margin: 40px 0 0 0;
  }
  .profile-options{
    display: flex;
    justify-content: space-between;
  }
  .profile-detail {
    margin: 25px 0 0;
  }
  .profile-options .highlighted-btn {
    margin: 0 !important;
  }
  .profile-options,
  .profile-details-role {
    text-align: center;
  }
}
@media screen and (max-width:810px) {
  .profile-picture {
    height: 275px;
    width: 275px;
    margin-top: -90px;
  }
  .profile-details{
    width: 90%;
  }
}

